import React, { Component } from 'react';

/**
 * Renders the Footer
 */
class Footer extends Component {

    render() {
        return (
            <footer className="footer">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-12 mt-6">
                            2021 Proyecto de clases
                        </div>
                    </div>
                </div>
            </footer>
        )
    }
}

export default Footer;